.card {
    background: #fff;
    border-radius: 2px;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  } 
  .card > .img-container {
    height: 80%;
    overflow: hidden;
    text-align: center;
  } 
  .card > .img-container > img {
    width: 100%
  } 
   .card{
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  } 